body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
}
.App{
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
  word-wrap: break-word;
}
.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d
}
.navbar h1{
  margin: 10px 0;
}

.book-list{
  margin: 20px;
  
}
.book-list ul{
  padding: 0;
  list-style-type: none;

}



.book-list li{
  background: #6d3d6d;
  border-radius: 4px;
  padding: 10px;
  margin: 10px 0;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
}
.book-list li.done{
  background: #3d1234;
  border-radius: 4px;
  padding: 10px;
  margin: 10px 0;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
}
 

.book-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}
.book-list .author{
  font-size: 0.9em;
  color: #ddd;
}


.book-list .title.done{
 color: #666
}
.book-list .author.done{
  color: #666;
}











.empty{
  margin: 20px;
  text-align: center;
}

form{
  padding: 20px;
}
input[type='text'], input[type='password']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
  border-radius: 4px;
  box-shadow: 1px 1px 5px 3px  rgb(54, 7, 40);
}




input[type='submit']{
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
  border-radius: 4px;
}

input[type='checkbox']{
  float:right;
  margin-right: 10px;
  zoom:180%;
}


.deleteBtn {
margin-top: 5px;
  background: #eee;
  border: 0;
  float: right;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
  /* padding: 6px 20px; */
   /* display: block;  */
}
